*{
  margin: 0;
  padding: 0;
}

.main_div{
  height: 100vh;
  width: 100%;
  background-color: #6983aa;
  display: flex;
  justify-content: center;
  align-items: center;
  
}

.center_div{
  height: 80vh;
  width: 50%;
  background-color: #f4f4f4;
  text-align: center;
  border-radius: 20px;
  box-shadow: 10px 10px 20px  rgb(113, 113, 216);

}

h1{
  text-shadow: 2px 2px #6983aa;
  margin-top: 15px;
  background-color:#6983aa ;
}

input{
  border: none;
  border-bottom: 1px solid #6983aa;
  text-align: center;
  height: 30px;
  width: 60%;
  background: transparent;
  font-size: 20px;
  top: 10px;
  bottom: 10px;
  outline: none;
}

button:hover {
  background-color:#20bf6b ;
}

ol{
  margin-top: 30px;
  text-align: left;
  font-size: 20px;
  align-items: center;
  text-transform: capitalize;
  list-style-type: none;
  margin-left: 5px;
  word-break: break-all;
  white-space: pre-wrap;
}

.todo_style{
  display: flex;
  justify-content: left;
  align-items: center;
}
.todo_style .fa-times{
  width: 20px;
  height: 20px;
  display: flex;
  justify-content: left;
  align-items: center;
  background-color: red;
  color: aliceblue;
  border-radius: 50%;
  margin: 0 15px 0 34px;
  box-shadow: 5px 5px 15px -5px rgb(0 , 0 , 0 , 0.3);
}





button{
  margin-left: 5px;
  min-height: 35px;
  width: 40px;
  font-size: 30px;
  border-radius: 50%;
  border: none;
  background-color:#6983aa ;
}
